$color-green: #4a611b;
$color-brown: #925a42;
$color-grey: #929598;
$color-blue: #447091;
$color-white: #D9D2C7;
$color-black: black;

:root {
    --green: #{$color-green};
    --brown: #{$color-brown};
    --grey: #{$color-grey};
    --blue: #{$color-blue};
    --white: #{$color-white};
    --black: #{$color-black};
}

$size: 1rem;

